import Data from './education.json';

const Education = () => {
    return(
        <>
        { Data.map((education) => (
            <div key={education.id} className="resume-item">
                <h4>{education.jurusan}</h4>
                <h5>{education.tahun}</h5>
                <p><em>{education.nama}</em></p>
                <p>{education.detail}</p>
            </div>
        ))}
        </>
    )
}

export default Education;